@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap);
body {
  background-color: rgb(241, 225, 255);
  font-family: 'Poppins', sans-serif;
}

.container {
  background-color: #fff;
  min-height: 640px;
  width: 360px;
  margin: 40px auto;
  border-radius: 30px;
}

header {
  background-color: #4044C9;
  border-radius: 30px 30px 0px 0px;
}

h2 {
  text-align: center;
  color: #fff;
  font-weight: 700;
  padding: 10px;
  margin: 0px;
}

form input {
  background-color: #fff;
  width: 220px;
  height: 45px;
  border: 0;
  border-radius: 10px;
  padding: 0px 10px;
  margin: 20px;
  font-size: 16px;
  color: black;
}

input:focus, input:active {
  outline: none;
}

form button {
  background-color: #fff;
  width: 60px;
  height: 45px;
  border: 0;
  border-radius: 10px;
  padding: 0px 5px;
  margin-right: 20px;
  font-size: 16px;
  color: #4044C9;
  cursor: pointer;
  font-weight: 500;
} 
.list-item {
    color: #fff;
    background-color: #4044C9;
    height: 40px;
    width: 320px;
    margin: 20px auto;
    border-radius: 14px;
}

.list-item p {
    padding: 10px;
    margin: 0px;
    position: relative;
}

.list-item p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
    font-size: 18px;
}

.list-item p input {
    background-color: transparent;
    color: #fff;
    font-size: 18px;
    border: 0;
}

input:focus, input:active {
    outline: none;
} 
